<!-- 搜索页 -->
<template>
<div class="search">
  <div class="search-left">
    <p class='serach-title'>{{title}}</p>
    <kb v-on:pressDown="keepDo" ref="kbc" ></kb>
    <div class="tip">输入拼音首写字母搜索相关游戏视频</div>
  </div>
  <div class="search-right">
    <!-- 页码 -->
    <div class="pageSize">第<span>{{pageNum}}</span>页</div>
    <!-- 搜索及推荐内容 -->
    <template v-if="isRecommend">
      <div class="recommendWrap">
        <div class="recommendTemp">
          <div class="boxWrap" v-for="(item,index) in recommendData" :key="index" :class="itemNo == index?'change':''">
              <lazy-image
              :src='item.elementImg'
              :placeholder='placeholder'
              :imgClass="round"
              :initScore = 'initScore'
              :title="item.elementName"
              :score="item.score"
              />
          </div>
        </div>
      </div>
    </template>
    <template v-else>
      <div v-for="(item,index) in eleList"  :key="item.id" :id="('a'+index)" class="search_ul">
        <div class="searchImgs">
          <div class="search_img" v-for="(assert,index) in item.resultList" v-if="index<8"   :key="assert.id" :id="('b'+index)">
            <lazy-image
            :src='assert.elementImg'
            :placeholder='placeholder'
            :imgClass="round"
            :initScore = 'initScore'
            :title="assert.elementName"
            :score="assert.score"
            />
          </div>
        </div>
      </div>
    </template>
    <!-- 滚动条 -->
    <div class="search-scroll">
      <div class="search-bar"></div>
    </div>
  </div>
  <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"  ref="keyDo"></keyDo>
</div>
</template>

<style scoped="search">
.searchImgs {
  position: relative;
  width: 900px;
}
.search {
  width: 1200px;
  height: 720px;
  overflow: hidden;
}
.search:after {
  clear: both;
  content: "";
  display: block;
}
.search_img .imglazy {
  border-radius: 15px;
}
.search_img div img.round {
  border-radius: 15px;
}
.search_ul {
  width: 100%;
  margin-top: 100px;
  margin-left: 35px;
  overflow: hidden;
  height: 550px;
}
.search_ul:after {
  clear: both;
  content: "";
  display: block;
}
.search_ul .search_img {
  width: 160px;
  height: 230px;
  margin-top: 32px;
  margin-left: 32px;
  position: relative;
  float: left;
}
.search-bar {
  position: relative;
  width: 11px;
  height: 56px;
  background-color: #ffffff;
  border: rgba(112, 112, 112, 1) 1px solid;
  border-radius: 10px;
  left: -6px;
  top: -1%;
}
.search-left {
  width: 297px;
  height: 720px;
  float: left;
  box-sizing: border-box;
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.4);
}
.search-right {
  position: relative;
  width: 896px;
  height: 720px;
  float: left;
  box-sizing: border-box;
}
.pageSize {
  position: absolute;
  top: 71px;
  right: 90px;
  height: 33px;
  width: 85px;
  font-family: "PingFang HK", "Ultralight";
  font-size: 24px;
  letter-spacing: 2px;
  font-weight: 300;

  color: #ffffff;
}
.serach-title {
  margin-top: 17px;
  font-size: 39px;
  margin-left: 9px;
  color: #ffffff;
  margin-bottom: 115px;
  font-family: "PingFang HK", "Ultralight";
  opacity: 0.6225;
  font-weight: 300;
}
.tip {
  width: 226px;
  height: 69px;
  text-align: left;
  font-size: 20px;
  padding: 25.5px 21.5px 18.5px 23.76px;
  background-color: #d36b6b;
  border: 1px solid #95989a;
  border-radius: 14.5px;
  color: #ffffff;
}
.tip::before {
  position: absolute;
  width: 0px;
  height: 0px;
  border-right: 10px solid transparent;
  border-bottom: 20px solid #d36b6b;
  border-left: 10px solid transparent;
  content: "";
  top: 516px;
}
.search-left .searchName {
  font-size: 20px;
  text-align: center;
  margin-top: 15px;
}
.search-left .introduce {
  font-size: 18px;
