}
</style>
<style>
.round {
  border-radius: 15px;
}
</style>

<script>
import { mapGetters } from "vuex";
import kb from "../../tool/keyboard/keyboard.vue";
import { HTTPUtil as api } from "../../fetch/api.js";
import { bp as bi } from "../../js/ga.js";
import lodash from 'lodash';

export default {
  props: ["jsonUrl", "type", "catId", "catCode"],
  name: "search",
  data() {
    return {
      isShow: true,
      col: 0,
      row: 0,
      isShow: false,
      sc: false,
      title: "搜索",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      eleList: [],
      keyword: "yh",
      oldF: [0, 0],
      newF: [],
      objindex: [],
      listLength: 0,
      round: "round",
      isRecommend: true,
      firstLoad: true,
      pageSize: 0,
      pageNum: 1,
      totalPage: 0,
      pageIndex: 0,
      moveDistance: 0,
      time: 0,
      recommendData:[],
      itemNo:-1,
      temporaryItemNo:0,
    };
  },
  components: {
    kb
  },
  activated() {
    console.log("keep-alive:search组件被激活");
    this.$store.commit("GET_HTTPRESPONSETIME", new Date().getTime());
    if (this.catCode != undefined) {
      // 当组件被复用时相关信息需要初始化
      this.firstLoad = true;
      this.isShow = false;
      this.removeCss();
      this.keyword = "";
      this.objindex = [0, 0];
      this.col = 0;
      this.row = 0;
      this.oldF = [0, 0];
      this.newF = [0, 0];
      $(".search-right .searchImgs").css("left", "0px");
      $(".search-right")
        .stop(false, true)
        .animate({ top: "0px" }, 300);
      console.log("激活小键盘");
      this.$refs.kbc.keyword = "";
      this.$refs.kbc.getKeyListen(0);
      this.$refs.kbc.isDel = false;
      try {
        let overTime = 0;
        if (this.firstLoad) {
          overTime = this.httpResponseTime - this.startTime;
          console.log("缓存搜索页第一次加载");
          this.firstLoad = false;
        }
        console.log(
          "搜索页面加载完成时间：" +
            overTime +
            "*******是否第一次加载页面：" +
            this.firstLoad
        );
        let traceId = "|" + this.catId + "|";
        let traceType = "|1|" + this.catCode + "|";
        bi.loadPage(traceId, traceType, 2, 0, 1, this.catId, 0, overTime);
      } catch (error) {
        console.log(error);
      }
    } else {
      console.log("搜索组件复用");
    }
  },
  created() {
    this.init();  
  },
  methods: {
    init() {
      let curl =
        "http://183.131.15.11:8490/?s=22|4&p=yhSearchRecommend&k=1&v=1";
      if (this.$route.query.jsonUrl) {
        curl = this.$route.query.jsonUrl + this.behindParams;
        this.childrenUrl = this.$route.query.jsonUrl;
      } else {
        curl += this.behindParams;
        this.getRecommendData(curl);
      }
    },
    getDetail(curl) {
      let that = this;
      if (this.isHTTPS && curl.indexOf("https") < 0) {
        curl = curl.replace("http", "https");
      }
      api
        .get(curl)
        .then(
          data => {
            let searchList = data.data;
            that.eleList = [];
            searchList.forEach(element => {
              if (
                element.resultList.length > 0 &&
                element.categoryName == "游戏"
              ) {
                that.pageSize = element.resultNum;
                that.totalPage = Math.ceil(element.resultNum / 8);
                that.moveDistance = Math.floor(100 / that.totalPage);
                that.eleList.push(element);
                that.pageNum = 1;
              }
            });
            that.$nextTick(function() {
              $(".search-bar")
                .stop(false, true)
                .animate({ top: "-1%" });
              that.$lazyImages.loadImage();
            });
          },
          response => {}
        )
        .catch(ex => {
          console.log("获取搜索数据失败，搜索关键字：" + that.keyword);
        });
    },
    getRecommendData(curl){
      let that = this;
      if (this.isHTTPS && curl.indexOf("https") < 0) {
        curl = curl.replace("http", "https");
      }
      api
        .get(curl)
        .then(
          data => {
            console.log('数据'+JSON.stringify(data.data))
            this.recommendData = [];
            this.recommendData = data.data.hotAssetList;
            that.totalPage = Math.ceil(data.data.hotAssetList.length / 4);
            that.moveDistance = Math.floor(100 / that.totalPage);
            that.eleList.push(data.data.hotAssetList);
            that.pageNum = 1;
            that.$nextTick(function() {
              $(".search-bar")
                .stop(false, true)
                .animate({ top: "-1%" });
              that.$lazyImages.loadImage();
            });
          },
          response => {}
        )
        .catch(ex => {
          console.log("获取搜索数据失败，搜索关键字：" + that.keyword);
        });
    },
    keyCode(params) {
      if (params == "down") {
        this.down();
      } else if (params == "up") {
        this.up();
      } else if (params == "left") {
        this.left();
      } else if (params == "right") {
        this.right();
      } else if (params == "KeyEnter") {
        let url = '';
        if(this.isRecommend){
          url = this.recommendData[this.itemNo].jsonUrl
        }else{
          url = this.eleList[0].resultList[this.col].jsonUrl;
        }
        console.log("详情页地址：" + url);
        this.$router.push({
          name: "GamePages",
          params: { jsonUrl: url, catId: this.catId }
        });
      } else if (params == "KeyBack") {
        this.KeyBack();
      }
    },
    KeyBack() {
      this.pageNum = 1;
      this.$router.go(-1);
    },
    down:lodash.debounce(function(){
      if(this.isRecommend){
        if(this.itemNo+4>this.recommendData.length-1)return;
        $('.recommendTemp').stop(false, true).animate({ top: "-=265px" });
        this.itemNo+=4;
        this.pageNum++;
        $(".search-bar")
        .stop(false, true)
        .animate({ top: "+=" + this.moveDistance + "%" });
      }else{
        if (this.col > 3 && this.pageNum < this.totalPage) {
          this.removeCss();
          this.pageIndex = this.pageNum * 8;
          this.pageNum++;
          this.getNextPage(this.pageIndex, 8);
          $(".search-bar")
            .stop(false, true)
            .animate({ top: "+=" + this.moveDistance + "%" });
        } else if (
          this.col < 4 &&
          this.col + 4 <= this.eleList[0].resultList.length - 1
        ) {
          this.removeCss();
          this.col += 4;
          this.addCss();
        }
      }
    },300),
    up:lodash.debounce(function(){
      if(this.isRecommend){
        if(this.itemNo<4)return;
        $('.recommendTemp').stop(false, true).animate({ top: "+=265px" });
        this.itemNo-=4;
        $(".search-bar")
        .stop(false, true)
        .animate({ top: "-=" + this.moveDistance + "%" });
        this.pageNum--;
      }else{
        if (this.col < 4 && this.pageNum > 1) {
          this.removeCss();
          this.pageNum--;
          console.log(this.pageNum);
          this.pageIndex -= 8;
          this.getNextPage(this.pageIndex, 8);
          $(".search-bar")
            .stop(false, true)
            .animate({ top: "-=" + this.moveDistance + "%" });
        } else if (this.col > 3) {
          this.removeCss();
          this.col -= 4;
          this.addCss();
        }
      }
    },300),
    left:lodash.debounce(function(){
      if(this.isRecommend){
        if (this.itemNo%4 == 0){
          this.isShow = false;
          this.$refs.kbc.getKeyListen(5);
          this.temporaryItemNo = this.itemNo;
          this.itemNo = -1;
          return;
        };
        this.itemNo--;
      }else{
        if (this.col == 0 || this.col == 4) {
          this.removeCss();
          this.isShow = false;
          this.$refs.kbc.getKeyListen(5);
          return;
        }
        this.removeCss();
        this.col--;
        this.addCss();
      }
    },300),
    right:lodash.debounce(function(){
      if(this.isRecommend){
        if (this.itemNo == this.recommendData.length - 1) return;
        if ((this.itemNo+1)%4 == 0)return;
        this.itemNo++;
      }else{
        if (this.col == this.eleList[0].resultList.length - 1) return;
        if (this.col == 7) return;
        this.removeCss();
        this.col++;
        this.addCss();
      }
    },300),
    addCss() {
      $("#b" + this.col).addClass("change");
      try{
        let p_title = $("#b" + this.col + " .s-title-name").text();
        if (p_title.length >= 8) {
          let id = "#b" + this.col;
          let speed = 50;
          let that = this;
          let tab = document.querySelector(id + " .s-title");
          let tab1 = document.querySelector(id + " .s-title-name");
          let tab2 = document.querySelector(id + " .s-title-temp");
          tab.style.textOverflow = "clip";
          tab2.innerHTML = tab1.innerHTML;
          this.timer = setInterval(function() {
            that.Marquee(tab, tab1, tab2);
          }, speed);
        }
      }catch(error){
        console.log(error)
      }
    },
    removeCss() {
      $("#b" + this.col).removeClass("change");
      try{
        if (this.timer != 0) {
          clearInterval(this.timer);
          this.timer = 0;
          let id = "#b" + this.col;
          let tab = document.querySelector(id + " .s-title");
          document.querySelector(id + " .s-title-temp").innerHTML = "";
          tab.scrollLeft = 0;
          tab.style.textOverflow = "ellipsis";
        }
      }catch(error){
        console.log(error)
      }
    },
    keepDo(bl, name, pos) {
      if (bl) {
        this.keyword = name;
      } else {
        if(this.isRecommend){
          this.itemNo = this.temporaryItemNo;
          this.isShow = true;
        }else{
          if (this.eleList.length > 0) {
            this.isShow = true;
            this.row = this.oldF[0];
            this.col = this.oldF[1];
            this.addCss();
            try {
              // 搜素结果中点击
              if (this.catCode == undefined) this.catCode = 0;
              if (this.catId == undefined) this.catId = 0;
              let traceId = "|" + this.catId + "|";
              let traceType = "|1|2|";
              bi.searchResultClick(
                this.keyword,
                1,
                traceId,
                traceType,
                1,
                this.catId,
                2,
                0
              );
            } catch (error) {
              console.log(error);
            }
          } else {
            this.isShow = false;
            this.$refs.kbc.getKeyListen();
          }
        }
      }
    },
    getNextPage(pageIndex, pageSize) {
      // 搜索接口翻页数据
      let curl =
        "http://183.131.15.11:8490/?s=22|4&p=yhSearch&k=1&v=1&actorType=6&word=" +
        this.keyword +
        "&pindex=" +
        pageIndex +
        "&psize=" +
        pageSize +
        this.behindParams;
      let that = this;
      if (this.isHTTPS && curl.indexOf("https") < 0) {
        curl = curl.replace("http", "https");
      }
      api.get(curl).then(json => {
        let searchList = json.data;
        that.eleList = [];
        searchList.forEach(element => {
          if (element.resultList.length > 0 && element.categoryName == "游戏") {
            that.eleList.push(element);
          }
        });
        that.$nextTick(function() {
          that.$lazyImages.loadImage();
          that.col = 0;
          that.addCss();
        });
      });
    },
    Marquee(tab, tab1, tab2) {
      let a = 0;
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        // tab.scrollLeft -= tab1.offsetWidth;
        tab.scrollLeft = 0; // 上面的写法会导致滚动时突然抖动
      } else {
        a = tab.scrollLeft;
        tab.scrollLeft++;
        if (a == tab.scrollLeft) {
          tab.scrollLeft = 0;
        }
      }
    }
  },
  watch: {
    keyword() {
      if(this.keyword == ''){
        this.isRecommend = true;
        this.init()
      }else{
        let curl = "";
        curl =
          "http://183.131.15.11:8490/?s=22|4&p=yhSearch&k=1&v=1&word=" +
          this.keyword +
          "&pindex=0&psize=8&actorType=6" +
          this.behindParams;
        this.isRecommend = false;
        this.col = 0;
        this.row = 0;
        this.oldF = [0, 0];
        this.newF = [0, 0];
        this.getDetail(curl);
      }
    },
    eleList() {
      if (this.eleList.length == 0) {
        // 没有搜索结果
        try {
          // 搜素结果中点击
          console.log("没有搜索结果");
          if (this.catCode == undefined) this.catCode = 0;
          if (this.catId == undefined) this.catId = 0;
          let traceId = "|" + this.catId + "|";
          let traceType = "|1|2|";
          bi.searchResultClick(
            this.keyword,
            2,
            traceId,
            traceType,
            1,
            this.catId,
            2,
            0
          );
        } catch (error) {
          console.log(error);
        }
      }
      this.$nextTick(function() {
        this.$store.commit("GET_HTTPRESPONSETIME", new Date().getTime());
        let overTime = 0;
        if (this.firstLoad) {
          overTime = this.httpResponseTime - this.startTime;
          console.log("搜索页第一次加载");
          this.firstLoad = false;
        } else {
          return "";
        }
        if (this.catCode == undefined) this.catCode = 0;
        if (this.catId == undefined) this.catId = 0;
        let traceId = "|" + this.catId + "|";
        let traceType = "|1|" + this.catCode + "|";
        bi.loadPage(traceId, traceType, 2, 0, 1, this.catId, 0, overTime);
      });
    }
  },
  computed: {
    ...mapGetters([
      "rootPath",
      "catList",
      "category",
      "behindParams",
      "isHTTPS",
      "startTime",
      "httpRequestTime",
      "httpResponseTime"
    ])
  },
  beforeRouteLeave(to, from, next) {
    try {
      console.log("开始时间被重置" + this.startTime);
      let overTime = 0;
      overTime = new Date().getTime() - this.httpResponseTime;
      if (this.catCode == undefined) this.catCode = 0;
      if (this.catId == undefined) this.catId = 0;
      let traceId = "|" + this.catId + "|";
      let traceType = "|1|" + this.catCode + "|";
      this.$store.commit("GET_STARTTIME", new Date().getTime());
      bi.closPage(traceId, traceType, 2, 0, 1, this.catId, 0, overTime);
      console.log("页面停留时间：" + overTime + "^^^^^^^focusId:" + this.catId);
    } catch (error) {
      console.log(error);
    } finally {
      next();
    }
  }
};
</script>



